import {forwardRef} from 'react';
import styles from './Frame.module.scss';
import Shape from '../Shape/Shape';

interface Props {
    isElements: number[][]
}

const Frame = forwardRef<HTMLTableElement, Props>(({isElements}, frameElement) => {
    return (
        <table ref={frameElement} className={styles['frame']}>
            <tbody>
                {isElements.map((shapes, i: number) => {
                    return (
                        <tr key={`${i}_frame_row`}>
                            {shapes.map((_, j: number ) => {
                                return (
                                    <Shape id={`${i}_${j}`} isActive={!!isElements[i][j]} key={`${i}_${j}_shape`} />
                                );
                            })}
                        </tr>
                    );
                })}
            </tbody>
        </table>
    );
});
export default Frame;
